const infinityCardAscii = `
AB AE AE IE
CD CF GH JH

KL KM KM PM
JH JN OD CD
`;

export const polygonsFromAsciiTiles = (
  tileWidth,
  tileHeight,
  gutterWidth,
  asciiCanvas,
) => {
  const tileData = asciiCanvas
    .trim()
    .split("\n")
    .map((v) => v.split(""));
  let rowSkips = 0;

  const polygons = tileData.reduce((memo, row, rowIndex) => {
    if (row.length == 0) rowSkips += 1;
    let colSkips = 0;
    row.forEach((tileId, colIndex) => {
      if (tileId === " ") {
        colSkips += 1;
        return;
      }

      memo[tileId] ||= {
        polygon: [
          [0, 0],
          [tileWidth, tileHeight],
        ],
        transforms: [],
      };
      memo[tileId].transforms.push({
        translate: [
          colIndex * tileWidth + colSkips * (gutterWidth - tileWidth),
          rowIndex * tileHeight + rowSkips * (gutterWidth - tileWidth),
        ],
      });
    });
    return memo;
  }, {});

  return Object.values(polygons);
};

export const genInfinityCardData = (tileWidth, gutterWidth) => {
  const polygons = polygonsFromAsciiTiles(
    tileWidth,
    tileWidth,
    gutterWidth,
    infinityCardAscii,
  );

  return {
    canvas: {
      width:
        tileWidth * infinityCardAscii.trim().split(`\n`)[0].length -
        3 * (tileWidth - gutterWidth),
      height:
        tileWidth * infinityCardAscii.trim().split(`\n`).length -
        1 * (tileWidth - gutterWidth),
    },
    polygons,
  };
};
